@import url(../../node_modules/bootstrap/dist/css/bootstrap.min.css);

@import url(../../node_modules/baguettebox.js/dist/baguetteBox.css);

body {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  min-width: 320px;
  color: #1d1d1d;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  padding: 100px 0;
}

h3 {
  font-family: 'Palatino Linotype', 'Book Antiqua', 'Palatino', serif;
  font-size: 42px;
  font-style: italic;
}

h4 {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-size: 44px;
  font-weight: 700;
  margin-bottom: 20px;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Montserrat-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Montserrat-Semibold.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
}

button:active,
button:focus {
  outline: none;
}

/* ============================= Header ============================== */

.logo {
  width: 69px;
  height: 62px;
}

.main-header {
  background: url(../img/bg-main.jpg) center/cover no-repeat;
  height: 100vh;
  min-height: 700px;
  position: relative;
}

.main-header .navbar-toggler {
  color: #fff;
}

.main-header .navbar-brand {
  font-size: 41px;
  font-weight: 600;
  color: #fff;
}

.main-header ul li a {
  font-size: 17px;
  font-weight: 600;
  color: #fff;
}

.main-header ul li a::after {
  content: '';
  display: block;
  width: 100%;
  background-color: #fff;
  height: 1px;
  transform: scale(0);
  transition: transform 0.2s cubic-bezier(0.64, 0.28, 0.48, 0.99);
}

.main-header ul li a:hover::after {
  transform: scale(1);
}

.main-header-content {
  position: absolute;
  top: 25%;
  left: 15%;
  max-width: 600px;
  color: #fff;
  padding: 20px;
}

.main-header-buttons {
  padding-left: 30px;
}

.main-header-buttons .btn {
  padding: 18px 40px;
  margin-right: 20px;
  opacity: .8;
  margin-top: 30px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.35);
  transition: opacity 0.2s cubic-bezier(0.64, 0.28, 0.48, 0.99);
}

.main-header-buttons .btn:hover {
  color: #fff;
  opacity: 1;
}

.btn {
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  line-height: 1;
  text-transform: uppercase;
}

.btn-pink {
  background-color: #f42276;
}

.btn-violet {
  background-color: #69328e;
}

.section-tabs .nav-pills {
  margin-bottom: 20px;
}

.section-tabs .nav-link {
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 14px 30px;
  color: #484848;
  line-height: 1;
}

.section-tabs .nav-link.active {
  background-color: #f42267;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.35);
}

/* ============================= section Watch ============================== */

.section-watch {
  background: url(../img/world-map.png) center/cover no-repeat;
}

.section-watch h3,
.section-watch h4 {
  color: #484848;
}

.btn-shadow {
  box-shadow: 0 10px 0 #ff5990;
  border-radius: 10px;
  padding: 12px 25px;
  transition: all 0.2s cubic-bezier(0.64, 0.28, 0.48, 0.99);
}

.btn-shadow:hover {
  color: #fff;
  box-shadow: 0 10px 0 #ff3f7f;
  background-color: #da1757;
}

/* ============================= section Progress ============================== */

.section-progress {
  background-color: #4b2d7f;
  color: #fff;
  font-size: 15px;
  padding: 0;
}

.section-progress .progress-item {
  padding-top: 80px;
  padding-bottom: 80px;
  transition: all .5s;
}

.section-progress .progress-item:hover {
  background-color: #40286a;
}

.section-progress .num {
  font-size: 50px;
  font-weight: 600;
  margin: 30px 0 20px;
}

.section-progress h4 {
  font-size: 30px;
  height: 72px;
  overflow: hidden;
}

.section-progress i {
  font-size: 70px;
}

/* ============================= section Lets ============================== */

.section-lets {
  background: url(../img/letsgrow.jpg) center/cover no-repeat;
  color: #fff;
}

.section-lets .btn {
  margin-top: 45px;
}

/* ============================= section Design ============================== */

.section-design h3,
.section-design h4 {
  color: #484848;
}

.section-design h3 {
  font-size: 33px;
}

.section-design span i {
  font-size: 60px;
  background: linear-gradient(45deg, rgba(255, 0, 32, 0.7) 0%, rgba(68, 26, 130, 0.8) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding-left: 15px;
}

.section-design h2 {
  font-size: 25px;
  font-weight: 600;
  color: #484848;
  margin: 10px 0;
}

.embed-responsive {
  position: relative;
  border-radius: 10px;
}

#videoPlayBtn {
  width: 100%;
  height: 100%;
  z-index: 1000;
  position: absolute;
  top: 0;
  cursor: pointer;
  background: url(../img/play.png) center no-repeat;
}

/* ============================= section Work ============================== */

.section-work {
  background-color: #f1f1f1;
}

.section-work .nav-pills {
  margin: 30px 0;
}

.section-work .gallery-item {
  margin-bottom: 20px;
}

.gallery-item a img {
  transition: all .25s ease;
}

.gallery-item a img:hover {
  box-shadow: 0 14px 30px -15px rgba(0, 0, 0, 0.75);
}

