/* Extra Small Devices, Phones */

@media only screen and (max-width: 992px) {
  .main-header ul li a::after {
    display: none;
  }

  .main-header-content {
    background-color: rgba(0, 0, 0, 0.2);
  }

  section {
    padding: 50px 0;
  }
}

@media only screen and (max-width: 991px) {
  .navbar-nav {
    background: linear-gradient(45deg, #ed1b33 0%, rgba(68, 46, 130, 0.8) 100%);
    position: relative;
    z-index: 1;
    padding: 0 15px;
  }
}

@media only screen and (max-width: 768px) {
  .main-header-content {
    left: 0;
    margin: 0 20px;
    max-width: 100%;
  }

  .main-header-text h3 {
    font-size: 36px;
  }

  .main-header-text h4 {
    font-size: 38px;
  }

  .section-progress .progress-item {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

@media only screen and (max-width: 600px) {
  .main-header-content {
    top: 90px;
  }

  .main-header-buttons {
    padding: 0;
  }

  .main-header-buttons .btn {
    display: block;
    width: 100%;
    margin-bottom: 15px;
  }

  .main-header-buttons .btn:last-child {
    margin-top: 0;
  }

  .btn-shadow {
    padding: 10px 15px;
  }
}

@media only screen and (max-width: 480px) {
  .main-header-text {
    text-align: center;
  }

  .main-header-text h3 {
    font-size: 30px;
  }

  .main-header-text h4 {
    font-size: 32px;
  }

  .section-watch .nav-pills .nav-item {
    margin-bottom: 10px;
    width: 100%;
  }

  .section-watch .nav-pills .nav-link {
    padding: 8px 16px;
  }
}

@media only screen and (max-width: 380px) {
  .main-header .navbar-brand {
    font-size: 30px;
  }

  .main-header-text {
    text-align: center;
  }

  .main-header-text h3 {
    font-size: 22px;
  }

  .main-header-text h4 {
    font-size: 32px;
  }
}